header {
  padding: 0 10px; }

.title {
  margin: 50px 10px 0;
  font-size: 3rem; }
  .title img {
    height: 3.2rem; }

.caption {
  margin-top: 20px;
  font-size: 1.8rem;
  display: inline-block; }

@media screen and (max-width: 425px) {
  .title {
    font-size: 2rem; }
    .title img {
      height: 2.2rem; }
  .caption {
    font-size: 1.2rem; } }

.button {
  width: fit-content;
  height: 44px;
  border: none;
  border-bottom: 4px solid #d8c547;
  border-radius: 15px;
  padding: 10px 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
  background: #F0DB4F;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #393E46;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none;
  position: absolute;
  top: calc(50% - 22px);
  left: calc(50% - 75px);
  transition: height .3s, border .3s, background-color .3s, box-shadow .3s, color .3s, top .3s;
  cursor: pointer;
  outline: none; }
  .button:hover {
    border-bottom: 4px solid #c0af3f;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
    background: #d8c547; }
  .button:active {
    height: 40px;
    box-shadow: none;
    border-bottom: none;
    top: calc(50% - 16px); }

@media screen and (max-width: 425px) {
  .button {
    font-size: 1.1rem;
    left: calc(50% - 70px); } }

.card_container {
  width: 90%;
  margin: 50px auto 0;
  display: flex;
  align-items: center;
  overflow-x: auto; }
  .card_container::-webkit-scrollbar {
    width: 12px; }
    .card_container::-webkit-scrollbar-thumb {
      border-radius: 8px;
      background-color: #F0DB4F;
      box-shadow: inset 0 0px 5px rgba(0, 0, 0, 0.6);
      cursor: pointer; }
      .card_container::-webkit-scrollbar-thumb:hover {
        background-color: #c0af3f; }
    .card_container::-webkit-scrollbar-track {
      background-color: transparent; }
  .card_container:first-child() {
    margin-left: 0; }

.card {
  width: 250px;
  min-width: 250px;
  height: 350px;
  margin: 20px 20px 40px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 0 #d8c547, 0 5px 15px rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  background: #F0DB4F;
  color: #222831;
  text-decoration: none;
  transition: background-color .3s, box-shadow .3s, color .2s, font-size .2s, margin .2s;
  cursor: pointer; }
  .card:hover {
    background: #d8c547;
    box-shadow: 0 4px 0 #c0af3f, 0 5px 15px rgba(0, 0, 0, 0.6); }
  .card:active {
    margin-top: 24px;
    margin-bottom: 36px;
    box-shadow: 0 0 0, 0 5px 15px rgba(0, 0, 0, 0.6); }
  .card_img_container {
    width: 100%;
    height: 210px;
    border-radius: 12px;
    background: #EEEEEE;
    display: flex;
    justify-content: center;
    align-items: center; }
  .card_img {
    width: 100%; }
  .card_title {
    font-size: 1.5rem; }
  .card:first-child {
    margin: 20px 20px 40px 0; }
    .card:first-child:active {
      margin-top: 24px;
      margin-bottom: 36px; }

@media (max-width: 1024px) {
  .card_container {
    margin: 40px auto; }
  .card {
    width: 200px;
    min-width: 200px;
    height: 280px;
    margin: 20px 15px 40px;
    padding: 15px; } }

@media (max-width: 768px) {
  .card_container {
    margin: 40px auto; }
  .card {
    width: 175px;
    min-width: 175px;
    height: 245px;
    margin: 20px 10px 40px;
    padding: 10px; } }

@media (max-width: 425px) {
  .card_container {
    width: 100%;
    margin: 30px auto;
    flex-wrap: wrap;
    justify-content: space-evenly;
    overflow-x: auto; }
    .card_container .card {
      margin: 20px 0; }
  .card {
    width: 150px;
    min-width: 150px;
    height: 210px;
    margin: 20px 0;
    padding: 8px; }
    .card_title {
      font-size: 1.2rem; } }

@media (max-width: 375px) {
  .card {
    width: 125px;
    min-width: 125px;
    height: 175px;
    margin: 20px 0;
    padding: 8px; } }

.quiz_container {
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }

.question {
  margin: 0 10px;
  letter-spacing: 1px;
  font-size: 1.8rem;
  display: inline-block;
  position: relative; }
  .question::after {
    content: "";
    width: 100%;
    height: 6px;
    border-radius: 3px;
    background: linear-gradient(#F0DB4F, #d8c547);
    position: absolute;
    left: 0;
    bottom: -12px; }

.answer_container {
  width: 550px;
  height: 366px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 50px 450px;
  grid-template-rows: repeat(4, minmax(50px, auto));
  column-gap: 50px;
  row-gap: 50px;
  align-items: center; }

.answer_index {
  width: 50px;
  height: 50px;
  border-bottom: 4px solid #d8c547;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
  background: #F0DB4F;
  color: #222831;
  line-height: 46px;
  font-size: 1.3rem;
  font-weight: bold; }

.answer_button {
  width: 350px;
  height: fit-content;
  min-height: 50px;
  border: 0px;
  border-bottom: 4px solid #C9C9C9;
  border-radius: 15px;
  padding: 15px 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
  background: #EEEEEE;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  color: #222831;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  transition: height .3s, border .3s,background-color .3s, box-shadow .3s, color .2s; }
  .answer_button:hover {
    background: #C9C9C9;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
    border-color: #b5b5b5; }
  .answer_button:active {
    height: calc(100% - 4px);
    min-height: 46px;
    margin-top: 4px;
    border: 0;
    box-shadow: none; }
  .answer_button-correct {
    background-color: #2AD24F;
    border-color: #26BA47;
    transform: scale(1.025); }
    .answer_button-correct:hover {
      background-color: #2AD24F;
      border-color: #26BA47; }
  .answer_button-wrong {
    background-color: #D2342A;
    border-color: #BA2F26;
    color: #C9C9C9; }
    .answer_button-wrong:hover {
      background-color: #D2342A;
      border-color: #BA2F26;
      color: #C9C9C9; }

.congratulation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .congratulation_title {
    margin: 0;
    font-size: 1.6rem; }
  .congratulation_points {
    height: 65px;
    margin-bottom: 3.2rem;
    border-radius: 20px;
    padding: 10px 20px;
    background-color: #EEEEEE;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: flex-start;
    color: #393E46;
    font-size: 2rem;
    font-weight: bold;
    position: relative; }
  .congratulation .button {
    position: static;
    transition: height .3s, margin .3s, border .3s, background-color .3s, box-shadow .3s, color .3s, top .3s; }
    .congratulation .button:active {
      margin-top: 4px;
      box-shadow: none; }

@media (max-width: 768px) {
  .answer_container {
    width: 500px;
    height: fit-content;
    grid-template-columns: 50px 350px;
    column-gap: 25px;
    row-gap: 40px; }
  .congratulation_title {
    margin: 0 10px;
    font-size: 1.4rem; } }

@media (max-width: 500px) {
  .answer_container {
    width: 100%;
    height: fit-content;
    padding: 0 10px;
    grid-template-columns: 40px 1fr;
    column-gap: 15px;
    row-gap: 30px; } }

@media (max-width: 425px) {
  .question {
    font-size: 1.3rem; }
  .answer_container {
    column-gap: 10px; }
  .answer_index {
    width: 40px;
    height: 40px;
    line-height: 36px;
    font-size: 1.1rem; }
  .answer_button {
    width: 100%; } }

.loading_container {
  width: 100%;
  height: 100%;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center; }

.loading_item {
  color: #ffffff;
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 200px auto;
  position: relative;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load 1.7s infinite ease, round 1.7s infinite ease;
  animation: load 1.7s infinite ease, round 1.7s infinite ease; }

@-webkit-keyframes load {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em; }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em; }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em; }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; } }

@keyframes load {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em; }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em; }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em; }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em; } }

@-webkit-keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

* {
  box-sizing: border-box; }

body {
  height: fit-content;
  min-height: calc(100vh - 50px);
  margin: 0;
  background-image: linear-gradient(135deg, #222831, #393E46);
  background-size: cover;
  background-repeat: no-repeat;
  color: #EEEEEE;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative; }

#root {
  width: 100%;
  height: 100%;
  min-height: calc(100vh - 50px); }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.highlight {
  color: #F0DB4F; }
